import * as React from "react"
import face from '../../static/gabriele_iurlaro_propic.jpg'
const IndexPage = () => {
  return (
  <div class="container mx-auto px-4">
    <div class="grid grid-rows-3 grid-flow-col gap-4 pt-3">
      <div class="row-span-3">
        <figure class="rounded-xl">
          <img class="w-48 h-48 rounded-full mx-auto" src={face}></img>
        </figure>
      </div>
      <div class="col-span-2">
        <h1 class="text-4xl">
          Gabriele Iurlaro
        </h1>
      </div>
      <div class="row-span-2 col-span-2">
        <p>Hi, I'm Gabriele Iurlaro, this site is under construction. You can see my work on <a href="https://gitlab.com/gabbiurlaro">gitlab</a> and <a href="https://github.com/gabbiurlaro/">github</a>.</p>
      </div>
    </div>
  </div>
  )
}

export default IndexPage;